this.showLoading = true



let verifyCode = typeof(this.$route.query.verify_request) == "undefined" ? getQueryString("verify_request") : this.$route.query.verify_request
let edit = typeof(this.$route.query.edit) == "undefined" ? getQueryString("edit") : this.$route.query.edit


if(verifyCode == null && localStorage.token != null){
verifyCode = localStorage.token
}

if(typeof(verifyCode) == 'undefined'){
let oauthUrl = 'https://openapi.yiban.cn/oauth/authorize?client_id='+this.client_id+'&redirect_uri='+this.redirect_uri+'&display=html';
location.href = oauthUrl;
}else{
(async () => {
localStorage.token = verifyCode
let data = await isVerifyApi(verifyCode)


if(data.status == 'oauth'){
this.showLoading = true
this.loadingText = data.info
location.href = 'https://openapi.yiban.cn/oauth/authorize?client_id='+this.client_id+'&redirect_uri='+this.redirect_uri+'&display=html';
}


if(data.status == 'old'){
localStorage.errorTitle = ''
localStorage.errorDescription = '2017~'
this.$router.push({ path:'/Error'})
return;
}

if(data.status == 'success'){
if(data.complete == true && edit != 1){
console.log("here:"+verifyCode)
this.showLoading = false
this.$router.push({ path:'/Success',query:{verify_request:verifyCode}})

}else{


this.yb_userid = data.info.yb_userid
