
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { isVerifyApi,getAllFormApi } from '../utils/apis'
import { getQueryString } from '../utils/function'
export default {
components: {
Divider,
FormPreview
},
methods: {

},
data () {
return {
realname: '',
list: [],
buttons1: [{
style: 'default',
text: '',
link: 'http://www.yiban.cn/forum/article/show/channel_id/70896/puid/5370552/article_id/24881003/group_id/0'
}, {
style: 'primary',
text: '',
link: "/?verify_request="+localStorage.token+"&edit=1",
}]
}
},
created:function(){
let verifyCode = typeof(this.$route.query.verify_request) == "undefined" ? getQueryString("verify_request") : this.$route.query.verify_request

if(typeof(verifyCode) == 'undefined'){
let oauthUrl = 'https://openapi.yiban.cn/oauth/authorize?client_id='+this.client_id+'&redirect_uri='+this.redirect_uri+'&display=html';
location.href = oauthUrl;
}else{
(async () => {
localStorage.token = verifyCode
let data = await getAllFormApi(verifyCode)
if(data.status == 'success'){
this.list = data.info
this.realname = data.extra.realname
}
})();

}
}
}



