import "whatwg-fetch"

// const base_url = 'http://localhost/dolphinphp/index.php'
const base_url = 'http://www.chddata.com/index.php'

const decrypts_api = base_url+'/yiban/api/decrypts'
const is_verify_api = base_url+'/yiban/api/isVerify'
const post_form_api = base_url+'/yiban/api/postForm'
const get_all_form_api = base_url+'/yiban/api/getAllForm'

export let decryptsApi = async(token) => {

	let response = await fetch(decrypts_api+`?verify_request=`+token,{
		method:'GET',
		mode:'cors'
	}).catch((error) => {
		console.log(error)
	})
	return await response.json().catch((error) => {
		console.log(error)
	})
}

export let isVerifyApi = async(token) => {

	let response = await fetch(is_verify_api+`?verify_request=`+token,{
		method:'GET',
		mode:'cors'
	}).catch((error) => {
		console.log(error)
	})
	return await response.json().catch((error) => {
		console.log(error)
	})
}

export let postFormApi = async(token,data) => {
	
	let response = await fetch(post_form_api + `?token=` + token,{
  	  method:'POST',
  	  body: data,
  	  mode:'cors'
  	}).catch((error) => {
		console.log(error)
	})
	return await response.json().catch((error) => {
		console.log(error)
	})	
}

export let getAllFormApi = async(token) => {
	
	let response = await fetch(get_all_form_api + `?token=` + token,{
  	  method:'GET',
  	  mode:'cors'
  	}).catch((error) => {
		console.log(error)
	})
	return await response.json().catch((error) => {
		console.log(error)
	})	
}


